/* global $,alert */
import React from 'react'
import fn from './Fn'
import axios from 'axios'
import moment from 'moment'
import PropTypes from 'prop-types';

import {setMember} from '../../actions/user'

class Membership extends React.Component {
  static contextTypes = { store: PropTypes.object.isRequired };

  constructor(props) {
    super(props)
  }

  render() {
    let that = this
    let mData = {}
    mData.membership = {
      phone: '',
      month: 1,
      bonusDay: 0,
      info: ''
    }

    const price = this.context.store.getState().data.price.value
    const membershipInfo = this.props.membershipInfo
    const userInfo = this.context.store.getState().user

    const priceByNum = {
      1: price.one,
      3: price.three,
      6: price.six,
      12: price.twelve
    }

    const changeMonth = (value) => {
      mData.membership.month = value
      const text = (value === 12) ? '1 nm' : (value + ' thng')

      $('#month').text(text)
      $('#total').val(fn.numberWithCommas(fn.priceWithCoupon(mData.coupon, priceByNum[mData.membership.month], mData.membership.month)) + ' ')
    }
    return (
      <div className='ui container'>

        <div id='membership-info' className='ui small modal '>
          <div className='header' style={{textAlign: 'center'}}>
            Thng tin membership
          </div>
          <div className='ui' style={{padding: '30px 10px'}}>
            <div id='membership-info-body' style={{minHeight: '200px'}}>
              {(membershipInfo.email) ? (<div>
                Email: <span style={{color: 'red'}}>{membershipInfo.email}</span>
                <br />
                <br />
                Trng thi: <span
                style={{color: 'red'}}>{membershipInfo.state === 'pending' ? 'ang x l' : membershipInfo.state}</span>
                <br />
                <br />
                {(userInfo.info) ? (<div>
                  Bt u: <span style={{color: 'red'}}>{moment(userInfo.info.start).format('DD-MM-YYYY')}</span>
                  <br />
                  <br />
                  Kt thc: <span style={{color: 'red'}}>{moment(userInfo.info.end).format('DD-MM-YYYY')}</span>
                  <br />
                  <br />
                  <button className='ui primary button'
                          onClick={() => {
                            $('.thanh-vien-1').modal('show')
                          }}
                  >Gia hn thm
                  </button>
                </div>) : ('')}
                <div>
                  Mi thng tin chi tit xin vui lng lin h qua s in thoi <strong style={{color: 'red'}}>098 499
                  5157</strong>
                </div>
              </div>) : ('')}
            </div>
          </div>
        </div>

        <div className='ui first coupled modal thanh-vien-1'>
          <div className='header' style={{textAlign: 'center'}}>
            ng k thnh vin Membership
          </div>
          <div className='ui two column stackable padded grid'>
            <div className='ui  column padded grid noPa'>
              <div className='actions eight wide column' onClick={() => changeMonth(1)}>
                <div className=' button centerInside'>
                  <div className='ui huge horizontal statistic'>
                    <div className='value'>
                      1
                    </div>
                    <div className='label'>
                      Thng
                    </div>
                  </div>
                </div>
              </div>
              <div className='actions eight wide column' onClick={() => changeMonth(3)}>
                <div className=' button centerInside'>
                  <div className='ui huge horizontal statistic'>
                    <div className='value'>
                      3
                    </div>
                    <div className='label'>
                      Thng
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className='ui  column padded grid noPa'>
              <div className='actions eight wide column' onClick={() => changeMonth(6)}>
                <div className=' button centerInside'>
                  <div className='ui huge horizontal statistic'>
                    <div className='value'>
                      6
                    </div>
                    <div className='label'>
                      Thng
                    </div>
                  </div>
                </div>
              </div>
              <div className='actions eight wide column' onClick={() => changeMonth(12)}>

                <div className=' button centerInside'>
                  <div className='ui huge horizontal statistic'>
                    <div className='value'>
                      1
                    </div>
                    <div className='label'>
                      Nm
                    </div>
                  </div>
                </div>

              </div>
            </div>
          </div>
          <div className='ui segment noMa'>
            <div className='ui padded grid'>
              <div className='five wide column '>
                <div className='row centerInside'>
                  <i className='huge thumbs outline up icon'/>
                </div>
                <div className='row'>
                  <h3 style={{textAlign: 'center'}}>Cc li ch khi ng k membership</h3>
                </div>
              </div>

              <div className='eleven wide column'>

                <div className='ui list'>
                  <div className='item'>
                    <i className='checkmark icon'/>
                    <div className='content'>
                      c tham gia vo tt c cc kha hc c trn website
                    </div>
                  </div>
                  <div className='item'>
                    <i className='checkmark icon'/>
                    <div className='content'>
                      c gio vin c kinh nghim hng dn
                    </div>
                  </div>
                  <div className='item'>
                    <i className='checkmark icon'/>
                    <div className='content'>
                      Cc kha hc c cp nhp thng xuyn
                    </div>
                  </div>
                  <div className='item'>
                    <i className='checkmark icon'/>
                    <div className='content'>
                      ng k cng lu gi cng gim: <br />
                      <span style={{marginLeft: '20px'}}>1 thng: <span
                        style={{color: 'red'}}>{fn.numberWithCommas(price.one)}  </span></span><br />
                      <span style={{marginLeft: '20px'}}>3 thng: <span
                        style={{color: 'red'}}>{fn.numberWithCommas(price.three)} </span></span><br />
                      <span style={{marginLeft: '20px'}}>6 thng: <span
                        style={{color: 'red'}}>{fn.numberWithCommas(price.six)} </span></span><br />
                      <span style={{marginLeft: '20px'}}>1 nm: <span
                        style={{color: 'red'}}>{fn.numberWithCommas(price.twelve)} </span></span><br />
                    </div>
                  </div>
                </div>

              </div>
            </div>
          </div>
        </div>

        <div className='ui large second coupled modal'>

          <div className='header noBor' style={{textAlign: 'center'}}>
            ng k thnh vin Membership
          </div>
          <div className='ui'>
            <h3 style={{textAlign: 'center'}}>Bn ang ng k gi <span id='month'
                                                                         style={{color: 'red'}}>{(mData.membership.month === 12) ? ('1 nm') : (mData.membership.month + ' thng')}</span>,
              hay chn hnh thc thanh ton pha di</h3>
          </div>

          <div className='ui top attached tabular menu' style={{marginBottom: '10px'}}>
            <a className='item active' data-tab='first'>Thanh ton trc tip</a>
            <a className='item' data-tab='second'>Chuyn khon ngn hng</a>
          </div>

          <div className='ui stackable grid'>
            <div className='eight wide column'>
              <div className='ui bottom attached tab segment noBor active' data-tab='first'>
                <h2>Vn phng ANABIM CO,.LTD</h2>
                <h3>a ch: 36B ng 554, Trng Chinh, ng a, H Ni</h3>
                <h3>in thoi lin h: <span style={{color: 'red'}}>0975 622 789</span> - <span style={{color: 'red'}}>0949 958 898</span>
                </h3>
              </div>
              <div className='ui bottom attached tab segment noBor' data-tab='second'>
                <h2>NGN HNG TMCP NGOI THNG THNG LONG (VIETCOMBANK)</h2>
                <h3>Phng giao dch Kim Lin   Ch Da  390 X n  H Ni</h3>
                <h3>- Ch ti khon: <span style={{color: 'red'}}>PHM C THNH</span></h3>
                <h3>- S ti khon: <span style={{color: 'red'}}>0491000064512</span></h3>
              </div>
            </div>
            <div className='eight wide column'>
              <div className='ui form'>
                <div className='field'>
                  <label>S in thoi (Chng ti s lin lc vi s ny khi thanh ton thnh cng)</label>
                  <div className='field'>
                    <input type='text' id='inputPhone' name='inputPhone'/>
                  </div>
                </div>
                <div className='field'>
                  <label>Ni dung nhn gi</label>
                  <textarea id='inputInfo' name='inputInfo'/>
                </div>
              </div>
            </div>
          </div>

          <div className='ui segment noBor noMa'>
            <div className='ui' style={{height: '40px'}}>
              <div className='ui form'>
                <div className='inline field actions'>
                  <div is class='ui labeled button' tabindex='0'>
                    <input type='text' placeholder='M gim gi' id='inputPromo' name='inputPromo'/>
                    <a className='ui basic inverted grey left pointing label'
                       onClick={() => {
                         let promoCode = $('#inputPromo').val()
                         axios.post('/api/coupon', {code: promoCode})
                           .then((res) => {
                             mData.coupon = res.data
                             $('#couponInfo').text(fn.infoCoupon(mData.coupon, mData.membership))
                             $('#total').val(fn.numberWithCommas(fn.priceWithCoupon(mData.coupon, priceByNum[mData.membership.month], mData.membership.month)) + ' ')
                           })
                           .catch((err) => {
                             console.log(err)
                           })
                       }}
                    >
                      <i className='large checkmark icon noMa'/>
                    </a>
                  </div>
                  <span style={{color: 'red'}} id='couponInfo'/>
                  <button className='ui right floated  green approve button'
                          onClick={() => {
                            let that = this;
                            let phone = $('#inputPhone').val()
                            if (checkPhoneNumber(phone)) {
                              if (mData.coupon) {
                                mData.membership.coupon = mData.coupon
                                if (mData.coupon.kind === 3) {
                                  mData.membership.bonusDay = fn.bonusDay(mData.coupon, mData.membership.month)
                                }
                              }
                              mData.membership.price = fn.priceWithCoupon(mData.coupon, priceByNum[mData.membership.month], mData.membership.month)
                              mData.membership.phone = phone
                              mData.membership.info = $('#inputInfo').val()
                              axios.post(
                                '/api/membership',
                                mData.membership
                              ).then((res) => {
                                that.context.store.dispatch(setMember(res.data))
                                alert('Bn  ng k thnh cng!')
                                location.reload();
                              }).catch((err) => {
                                console.log(err)
                              })
                            } else {
                              $('.second.modal')
                                .modal('show')
                            }
                          }
                          }
                  >Xc nhn
                  </button>
                  <div is class='ui red labeled right floated button' tabindex='0'>
                    <a className='ui basic inverted grey right pointing label'>
                      Total
                    </a>
                    <input id='total' style={{color: 'red'}} disabled type='text'/>
                  </div>

                </div>

              </div>

            </div>
          </div>

        </div>

      </div>
    )
  }
}

export default Membership

var checkPhoneNumber = function (phone) {
  if (!phone || !phone.match(/\d/g)) {
    alert('Bn phi nhp s in thoi')
  } else if (phone.match(/\d/g).length === 10 || phone.match(/\d/g).length === 11) {
    return true
  } else {
    alert('S in thoi in cha ng nh dng, ( phi c 10 hoc 11 s )')
    return false
  }
}



